import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

// 购物车相关逻辑
const getShopId = () => {
  return useRoute().params.id
}
const getCartList = () => {
  return useStore().state.cartList
}
const getShopName = () => {
  return getCartList()?.[getShopId()]?.shopName
}
const getProductList = () => {
  return getCartList()?.[getShopId()]?.productList
}
const useCommonCartEffect = () => {
  const store = useStore()
  const changeCartItemInfo = (shopId, productId, productInfo, num) => {
    store.commit('changeCartItemInfo', {
      shopId,
      productId,
      productInfo,
      num
    })
  }
  const changeShopName = (shopId, shopName) => {
    store.commit('changeShopName', {
      shopId,
      shopName
    })
  }
  const changeCartItemChecked = (shopId, productId) => {
    store.commit('changeCartItemChecked', {
      shopId,
      productId
    })
  }
  const cleanCartProducts = (shopId) => {
    store.commit('cleanCartProducts', { shopId })
  }
  const setCartItemChecked = (shopId, checked) => {
    store.commit('setCartItemChecked', {
      shopId,
      checked
    })
  }
  return {
    changeShopName,
    changeCartItemInfo,
    changeCartItemChecked,
    cleanCartProducts,
    setCartItemChecked
  }
}

export {
  getShopId,
  getCartList,
  getShopName,
  getProductList,
  useCommonCartEffect
}
